     .flex {
         display: flex;
         justify-content: space-between;
         align-items: center;
         margin: 0 50px;
         border-radius: 100px;
         z-index: 0;
     }

     .box_container {

         text-align: center;

         min-height: 100%;

     }

     .general_container {

         position: relative;
         background-position: center;
         background-repeat: no-repeat;

     }

     .firstBack {

         width: 100%;
         height: 100%;
         border-radius: 50%;
         box-shadow: 0px -8px 0px 0px rgba(0, 0, 0, 0.56) inset;
         -webkit-box-shadow: 0px -8px 0px 0px rgba(0, 0, 0, 0.56) inset;
         -moz-box-shadow: 0px -8px 0px 0px rgba(0, 0, 0, 0.56) inset;
     }

     .secondBack {

         width: 75%;
         height: 75%;
         position: absolute;
         background-color: #fff;
         border-radius: 50%;
         transform: translate(-50%, -50%);
         left: 50%;
         top: 50%;
         display: flex;
         justify-content: center;
         align-items: center;
         -webkit-box-shadow: inset 0px 20px 0px -11px rgba(199, 187, 199, 1);
         -moz-box-shadow: inset 0px 20px 0px -11px rgba(199, 187, 199, 1);
         box-shadow: inset 0px 20px 0px -11px rgba(199, 187, 199, 1);
     }

     .backImage {
         width: 50px;
         height: 50px auto;

     }

     .backImage img {
         width: 100%;
         height: 100%;
         object-fit: cover;
     }


     .container {

         position: relative;
         width: 200px;
         height: 200px;

     }

     @media screen and (max-width:750px) {

         .flex {
             margin: 0 30px;
         }

         .container {

             position: relative;
             width: 150px;
             height: 150px;

         }
     }

     @media screen and (max-width:550px) {

         .flex {
             margin: 0 30px;
         }

         .container {

             position: relative;
             width: 120px;
             height: 120px;

         }
     }

     @media screen and (max-width:450px) {

         .flex {
             margin: 0 30px;
         }

         .container {

             position: relative;
             width: 100px;
             height: 100px;

         }
     }